<template>
  <section class="section">
    <h2 class="section-title">Introduccin</h2>
    <div class="section-content">
      <p>Hola!, soy desarrollador enfocado al rea web y con cierta especialidad en el Front-End, por lo que tengo a mi disposicin habilidades con los lenguajes del lado del cliente HTML, CSS y JavaScript, no obstante, tambin poseo conocimientos de tecnologas Back-End siendo capaz de desarrollar aplicaciones y sitios webs funcionales completos utilizando lenguajes como PHP y el entorno de ejecucin de JavaScript (NodeJS).</p>
      <p>
        As tambin utilizo diversos frameworks y libreras de desarrollo como Laravel (PHP), Express (NodeJS), SailsJS(NodeJS), ReactJS y VueJS(Javascript), Bootstrap, Bulma y Fundation (CSS).
        A lo largo de mi ejercicio profesional he tenido la oportunidad de desarrollar plataformas y aplicaciones Webs con diversos fines como blogs, e-commerce, portafolios en linea... utilizando CMS como Wordpress, Prestashop y Magento.
      </p>
      <p>Soy emprendedor, lo cual me ha permitido mejorar la comunicacin de equipo y con el pblico, adems, la versatilidad debe ser caracterstica de todo desarrollador; saber adaptarse a nuevas situaciones y tecnologas es parte de la cotidianidad en esta rea. He participado en distintos eventos como hackatones donde incluso junto a un equipo multidiciplinario; ganamos el primer lugar del StartupWeekend Maracay Venezuela (ver en reconocimientos).</p>
      <p>Llevo gran pasin por el desarrollo, sus metodologas, por lo que me he inverso en una rutina autodidacta y proactiva, donde la responsabilidad es mi carta de presentacin.</p>
    </div>
  </section>
</template>

<script>
export default {
  name: "introduction"
};
</script>

